import * as React from "react";
import { useState, useEffect } from "react";
import { HomeStyled, TeamStyled, CourseStyled, BlogStyled } from "./styled";
import { Container, CardTeam, CardCourse, CardBlog } from "../../components";
import Slider from "react-slick";

const HomeContainer = () => {
  const [nav1, setNav1] = useState();
  const [nav2, setNav2] = useState();
  const [navContent, setnavContent] = useState(["Join", "Skill Up", "Blog"]);

  return (
    <React.Fragment>
      <HomeStyled>
        <div className="potlet-bg">
          <Container>
            <div className="nav">
              <div className="nav-logo"></div>
              <div className="nav-row">
                <div className="nav-content">Home</div>
                <div className="nav-content">Join</div>
                <div className="nav-content">Skill up</div>
                <div className="nav-content">Blog</div>
              </div>
            </div>

            <div className="potlet-row">
              <div className="potlet-column">
                <div className="potlet-content">
                  <div className="label first"></div>
                  <div className="label second"></div>
                  <div className="label thrid">!</div>
                </div>
                <div className="login-btn-container">
                  <button className="btn-login"></button>
                </div>
              </div>
              <div className="potlet-logo"></div>
            </div>

            <div className="slider-container">
              <div className="slider-header"></div>
              <div className="slider-row">
                <div className="slider-column">
                  {/* <h4>First Slider</h4> */}
                  <Slider
                    asNavFor={nav2}
                    ref={(slider) => setNav1(slider)}
                    arrows={false}
                    fade={true}
                  >
                    <div className="slider-left">
                      <div className="slider-content-header">
                         <br />
                        !
                      </div>
                      <div className="slider-label">
                        
                         Start up
                      </div>
                      <div className="silder-btn">
                        <div className="btn-info"></div>
                      </div>
                    </div>
                    <div className="slider-left">
                      <div className="slider-content-header">
                        
                        <br />
                        !
                      </div>
                      <div className="slider-label">
                        
                        
                      </div>
                      <div className="silder-btn">
                        <div className="btn-info"></div>
                      </div>
                    </div>
                    <div className="slider-left">
                      <div className="slider-content-header">
                        
                        <br />
                        
                      </div>
                      <div className="slider-label">
                        
                        
                      </div>
                      <div className="silder-btn">
                        <div className="btn-info"></div>
                      </div>
                    </div>
                  </Slider>
                </div>
                {/* <h4>Second Slider</h4> */}
                <div className="slider-column">
                  <Slider
                    dots={true}
                    asNavFor={nav1}
                    ref={(slider) => setNav2(slider)}
                    swipeToSlide={true}
                    autoplay={true}
                    autoplaySpeed={8000}
                    arrows={false}
                    appendDots={(dots) => (
                      <div
                        style={{
                          bottom: "100%",
                          width: "auto",
                          left: "0",
                        }}
                      >
                        <ul style={{ margin: "0px" }}> {dots} </ul>
                      </div>
                    )}
                    customPaging={(i) => (
                      <div
                        style={{
                          width: "auto",
                          color: "#FC5336",
                          fontSize: "36px",
                          fontWeight: "bold",
                        }}
                      >
                        {navContent[i]}
                      </div>
                    )}
                  >
                    <div className="slider-pic">
                      <img src="images/nav_pic_1.png" alt="" />
                    </div>
                    <div className="slider-pic">
                      <img src="images/nav_pic_2.png" alt="" />
                    </div>
                    <div className="slider-pic">
                      <img src="images/nav_pic_3.png" alt="" />
                    </div>
                  </Slider>
                </div>
              </div>
            </div>
          </Container>
        </div>

        <Container className="Team">
          <div className="potlet-row header">
            <div className="potlet-header">!</div>
            <div className="potlet-link"></div>
          </div>
          <TeamStyled>
            <div className="team-card-container">
              <CardTeam
                className={"test"}
                pic={"images/card_team_1.png"}
                name={""}
                address={"KMUTT"}
                roleName={"PM"}
                status={""}
                statusCount={1}
              />
              <CardTeam
                className={"test"}
                pic={"images/card_team_2.png"}
                name={""}
                address={"KMUTT"}
                roleName={"PM"}
                status={""}
                statusCount={1}
              />
              <CardTeam
                className={"test"}
                pic={"images/card_team_3.png"}
                name={""}
                address={"KMUTT"}
                roleName={"PM"}
                status={""}
                statusCount={1}
              />
              <CardTeam
                className={"test"}
                pic={"images/card_team_4.png"}
                name={""}
                address={"KMUTT"}
                roleName={"PM"}
                status={""}
                statusCount={1}
              />
            </div>
          </TeamStyled>
          <div className="potlet-row header">
            <div className="potlet-header"></div>
            <div className="potlet-link"></div>
          </div>
          <CourseStyled>
            <CardCourse
              className={"test"}
              pic={"images/card_course_1.png"}
              title={"Exploratory Data Analysis with Google Sheets"}
              name={".  "}
              review={"6"}
              hour={"8 - 10"}
              price={"2,990"}
              star={3}
            />
            <CardCourse
              className={"test"}
              pic={"images/card_course_2.png"}
              title={"Essential CSS for Web Developers "}
              name={" "}
              review={"10"}
              hour={"8 - 10"}
              price={"2,990"}
              star={4}
            />
            <CardCourse
              className={"test"}
              pic={"images/card_course_3.png"}
              title={"Power BI Essentials"}
              name={" "}
              review={"6"}
              hour={"6 - 8"}
              price={"1,990"}
              star={5}
            />
          </CourseStyled>
          <div className="potlet-row header">
            <div className="potlet-header"></div>
            <div className="potlet-link"></div>
          </div>
          <BlogStyled>
            <CardBlog
              className={"test"}
              pic={"images/card_blog_1.png"}
              title={"Review Hackathon 2020"}
              name={"Nomad"}
              description={""}
              review={"20"}
              star={5}
            />
            <div className="line-vertical" />
            <CardBlog
              className={"test"}
              pic={"images/card_blog_2.png"}
              title={" Start up"}
              name={"Unknow"}
              description={
                " start up   start up "
              }
              review={"10"}
              star={4}
            />
          </BlogStyled>
          <BlogStyled>
            <CardBlog
              className={"test"}
              pic={"images/card_blog_3.png"}
              title={"Review Hackathon 2018"}
              name={" "}
              description={
                "   hackathon  -w-"
              }
              review={"5"}
              star={4}
            />
            <div className="line-vertical" />
            <CardBlog
              className={"test"}
              pic={"images/card_blog_4.png"}
              title={""}
              name={" "}
              description={
                " "
              }
              review={"2"}
              star={5}
            />
          </BlogStyled>
        </Container>
      </HomeStyled>
    </React.Fragment>
  );
};

export default HomeContainer;
